.he-wrap {
  position: relative;
  zoom: 1;
  display: inline;
}

.he-view,
.he-zoom {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  visibility: hidden;
}

.he-zoom img {
  position: absolute;
  top: 0;
  left: 0;
}

.he-zoomstate {
  position: absolute;
  bottom: 0;
  right: 0;
  background: #c00;
  color: #fff;
  border-radius: 3px 0 0;
  font-size: 12px;
  line-height: 16px;
  padding: 0 4px;
  box-shadow: -1px -1px 1px rgba(204, 0, 0, .3);
}

.he-view-show {
  visibility: visible;
}

.he-sliders {
  overflow: hidden;
}

.he-sliders img {
  display: none;
}

.he-sliders img:first-child {
  display: inline-block;
  display: inline;
  zoom: 1;
}

.he-pre,
.he-next {
  position: absolute;
  top: 50%;
  left: 0;
  width: 61px;
  height: 35px;
  margin-top: -18px;
  //background: url("../../images/nav.png") no-repeat top left;
  cursor: pointer;
}

.he-next {
  left: auto;
  right: 0;
  background-position: top right;
}

.a0,
.a1,
.a2,
.a3,
.a4,
.a5,
.a6,
.a7 {
  -webkit-animation-fill-mode: both;
  -moz-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: .6s;
  -moz-animation-duration: .6s;
  -o-animation-duration: .6s;
  animation-duration: .6s;
}

.a1 {
  -webkit-animation-delay: .2s;
