#footer-wrap {
  padding-top: 60px;
  padding-bottom: 60px;
  background: $footer_background_color;

  p {
    color: $footer_text_color;
  }

  h4 {
    color: $white;
  }

  i {
    font-size: 30px;
    color: $footer_text_color;
    padding-right: 25px;

    &:hover {
      color: $footer_icon_hover;
    }
  }
}



