/***********************************************************************************
* Service
***********************************************************************************/
#service {
  margin-top: 100px;
  margin-bottom: 100px;

  i {
    color: #00b3fe;
    font-size: 60px;
    padding: 15px;
  }
}

/***********************************************************************************
* Home Middle Content
***********************************************************************************/
#home-middle-content {
  margin-top: 80px;
  margin-bottom: 80px;
}

/***********************************************************************************
* Testimonial
***********************************************************************************/
#testimonial_wrap {
  background: url(../../images/testimonial-background.jpg);
  margin-top: 0;
  padding-top: 60px;
  padding-bottom: 80px;
  text-align: center;
  min-height: 450px;
  width: 100%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;

  #carousel-testimonials {
    .carousel-indicators {
      position: absolute;
      bottom: -20px;
      left: 50%;
      z-index: 15;
      width: 60%;
      margin-left: -30%;
      padding-left: 0;
      list-style: none;
      text-align: center;
    }

    .carousel-inner {
      padding: 0 50px;
    }

    .carousel-control {
      text-shadow: none;
      filter: alpha(opacity=100);
      position: absolute;
      opacity: 1;
      top: 0;
      bottom: 0;
      width: 0;

      .fa-arrow-left,
      .fa-arrow-right {
        position: absolute;
        top: 50%;
        margin-top: -10px;
        z-index: 5;
        display: inline-block;
      }

      .left {
        left: 0;

        .fa-arrow-left {
          left: 50%;
          margin-left: -10px;
        }
      }

      .right {
        right: 0;

        .fa-arrow-right {
          right: 50%;
          margin-right: -10px;
        }
      }
    }

    .carousel-control.left,
    .carousel-control.right {
      background-image: none;
    }
  }

  i {
    font-size: 50px;
    color: $white;
    margin-bottom: 25px;
  }

  p {
    color: $white;
    font-size: 15px;
    line-height: 30px;
  }

  h4 {
    color: $white;
  }
}

/***********************************************************************************
# Clients
***********************************************************************************/
#clients-wrap {
  background: $clients_background_color;
  margin-top: 0;
  padding-top: 80px;
  padding-bottom: 100px;

  h3 {
    margin-bottom: 60px;
  }
}



